import React from "react"
import { graphql, Link } from "gatsby"
import SEO from "../components/seo"
import Layout from "../components/layout"
import Navbar from "../components/navbar"
import NewsletterSubscribe from "../components/newsletterSubscribe"
import Heading from "react-bulma-components/lib/components/heading"
import NewsletterSummary from "../components/newsletterSummary"
import Container from "react-bulma-components/lib/components/container"

export default ({ data }) => {
  return (
    <Layout>
      <SEO title="Archiv" keywords={[`ecotainment`, `newsletter`, `archiv`]} />
      <Container>
        <Navbar />
        <NewsletterSubscribe />
        <Heading size={3} renderAs="h2" style={{ padding: "25px" }}>
          Archiv
        </Heading>
        {data.allNewJson.edges.map(({ node }, index) => (
          <Link to={`/${node.slug}`}>
            <NewsletterSummary node={node} />
          </Link>
        ))}
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query {
    allNewJson(sort: { fields: published, order: DESC }) {
      edges {
        node {
          published
          title
          slug
          tags
          newsletter {
            welcome
            intro
            parts {
              emoji {
                label
                icon
              }
              title
              tags
              text
            }
            outro
          }
        }
      }
    }
  }
`
